<template>
  <div>
    <!--  -->
    <div id="main" style="width: 100%; height: 800px"></div>
    <!--  -->
    <div id="info-box" style="margin-top: 20px; display: flex; justify-content: space-between; align-items: center;">
      <div class="content">
        <p><strong></strong> {{ selectedLocation.address }}</p>
        <p><strong></strong> {{ selectedLocation.people }}</p>
      </div>
      <!--  -->
      <button @click="resetMap" class="btn">
        
      </button>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import "@/js/ChinaMapchain.js";

export default {
  data() {
    return {
      tableData: [], // JSON
      selectedLocation: { address: '', people: '' }, // 
      mapInstance: null, // 
      initialOption: null // 
    };
  },
  created() {
    //  created  fetch 
    fetch(process.env.BASE_URL + "assets/json/ChinaMapPoint.json")
      .then(response => response.json())
      .then(data => {
        //  tableData 
        this.tableData = data.map(item => ({
          name: item.name,
          value: item.value,
          address: item.address, //  address
          people: item.people //  people
        }));

        //  tableData 
        console.log("tableData:", this.tableData);

        //  DOM 
        this.$nextTick(() => {
          this.initChart();
        });
      })
      .catch(error => {
        console.error("Error fetching JSON data:", error);
      });
  },
  methods: {
    initChart() {
      //  ECharts 
      this.mapInstance = echarts.init(document.getElementById('main'));

      // 
      this.initialOption = {
        tooltip: {
          show: true,
          triggerOn: 'click',
          formatter: function (params) {
            return params.data.address;
          }
        },
        geo: {
          map: 'china',
          roam: true, // 
          zoom: 1.2,
          center: [105, 36],
          itemStyle: {
            areaColor: '#f5f2f2',
            borderColor: "#835f5f",
          },
          label: {
            show: true,
            fontSize: "11.5",
            color: "rgb(107,102,102)"
          },
          emphasis: {
            itemStyle: {
              areaColor: '#d0a3a3',
            },
            label: {
              show: true,
              color: "rgb(255,255,255)"
            }
          }
        },
        series: [
          {
            name: '',
            type: 'scatter',
            coordinateSystem: 'geo',
            data: this.tableData,
            symbolSize: 12,
            itemStyle: {
              color: "#f13434",
              shadowBlur: 2,
              shadowColor: "#333"
            }
          },
          {
            name: "",
            type: "effectScatter",
            coordinateSystem: "geo",
            data: this.tableData,
            symbolSize: 4,
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
              color: "#f13434",
              period: 10,
              scale: 10
            },
            emphasis: {
              scale: true,
            },
            label: {
              formatter: "{b}",
              position: "top",
              show: true,
              fontSize: "10",
            },
            itemStyle: {
              color: "#f13434",
              shadowBlur: 2,
              shadowColor: "#333"
            }
          }
        ]
      };

      // 
      this.mapInstance.setOption(this.initialOption);

      // 
      this.mapInstance.on('click', (params) => {
        if (params.componentType === 'series') {
          // 
          this.selectedLocation = {
            address: params.data.address,
            people: params.data.people
          };
        }
      });
    },
    resetMap() {
      // 
      if (this.mapInstance && this.initialOption) {
        this.mapInstance.setOption(this.initialOption);
      }
    }
  }
};
</script>

<style scoped>
#info-box {
  background-color: #f9f9f9;
  border-radius: 8px;
  font-size: 20px;
  line-height: 1.6;
  border: 1px solid #ccc; /*  */
  font-family: ;
  color: black;
}
.content {
  margin-left: 20px;
}
.btn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: rgb(30, 50, 100);
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  margin-right: 20px;
  box-sizing: border-box;
  height: 40px;
  line-height: 20px;
}

.btn:hover {
  color: #ff9900;
}
</style>
